// @flow

import React from 'react'
import {graphql} from 'gatsby'
import {I18n} from 'aws-amplify'
import CookieConsent from 'react-cookie-consent'

import Main from '../components/main'

import type {MainProps} from '../components/main'

const Index = (props: MainProps) => {
  const {
    data: {
      site: {
        siteMetadata: {
          author: {summary: authorSummary},
          social: {email, telegram, github, youtube},
        },
      },
      allMarkdownRemark: {edges: posts},
    },
    location,
  } = props

  return (
    <>
      <Main
        title={I18n.get('allPosts')}
        authorSummary={authorSummary}
        email={email}
        telegram={telegram}
        github={github}
        youtube={youtube}
        posts={posts}
        location={location}
      />
      <CookieConsent buttonText={I18n.get('ok')}>{I18n.get('cookieConsent')}</CookieConsent>
    </>
  )
}

export default Index

// , filter: {frontmatter: {tags: {in: "banana"}}}

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        author {
          summary
        }
        social {
          github
          email
          youtube
          telegram
        }
      }
    }
    allMarkdownRemark(sort: {fields: [frontmatter___date], order: DESC}, limit: 13) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date
            title
            coverImage {
              src
              owner {
                name
                link
              }
              platform {
                name
                link
              }
            }
          }
        }
      }
    }
  }
`
